.wrapper {
  max-width: 1200px;
  color: rgb(92, 91, 91);
  font-weight: 500;
  margin: 0 auto;
  min-height: 100vh;
  margin-top: 0.8rem;
  background-clip: padding-box;
  border: 0.0625rem solid #e2e6ee;
  border-radius: 0.55rem;
  box-shadow: 2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff
}
.main {
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
  margin: 0.5rem;
  border: 0.0625rem solid #D1D9E6;
  padding: 0.7rem;
  border-radius: 0.55rem;
}
.inside-wrapper {
  box-shadow:  2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff;
  padding: 0.5rem;
 
  border-radius: 0.55rem;
}
.grid {
  display: grid;
  column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(4,1fr);
  text-align: center;

}
.grid img {
  width: 180px;
  height: 150px;
}
.grid img:hover {
  transform: scale(1.2);
  transition: 300ms ease-in-out;
}
.grid2 {
  display: grid;

  border-radius: 0.55rem;
  column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(2,1fr);
  text-align: center;
}
.grid2-1 {
  color: red !important;
}
h3 {
  font-size: 0.875rem;
  padding: 1rem 1.5rem;
  border-radius: 0.55rem;
  border: 0.0625rem solid #D1D9E6;
  border-radius: 0.55rem;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;

}
.home {
  display: flex;
  flex-direction: column;
  text-align: center;
  word-break: break-all;
  padding: 1rem;
  border-radius: 0.5rem;
  color: #f8f8ff;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='16' viewBox='0 0 12 16' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 .99C4 .445 4.444 0 5 0c.552 0 1 .45 1 .99v4.02C6 5.555 5.556 6 5 6c-.552 0-1-.45-1-.99V.99zm6 8c0-.546.444-.99 1-.99.552 0 1 .45 1 .99v4.02c0 .546-.444.99-1 .99-.552 0-1-.45-1-.99V8.99z' fill='%23908a99' fill-opacity='0.5' fill-rule='evenodd'/%3E%3C/svg%3E");
  opacity: 0.9;
  width: 100%;
  height: 80%;
}
.home p {
  font-size: 36px;
  margin-top: 0.5rem!important;
  font-weight: bold;
  margin: 0;
}
.home h2 {
  overflow: hidden;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 45px;
  font-family: 'Popins', serif;
  background-color: black;
  margin-top: -4.5rem!important;
}
.home h1 {
  font-size: 96px;
  font-family: 'Playfair Display', serif;
  margin-top: -0.2rem!important;
  padding: 15px 20px;

}
.home .typed {
  color:#f5ec43;
}
.about {
  display: grid;
  grid-template-columns: repeat(1,1fr);
  display: grid;
  margin: auto 0;
  transform: translate(-40px);
  place-items: center;
  margin-top: 20px;
  /* text-shadow:  5px 9px 5px #b8b9be,  -1px -1px 7px #ffffff !important;
  font-weight: 700; */
}
.about h1 {
  z-index: 99;
  color:#e2e6ee ;
}
.about .skills {
  min-height: 20vh;
  width: 50%;
  margin-left: 100px;
  margin-bottom: 20px;
}
.about .frameworks {
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;
  display: grid;
  height: 30vh;
  grid-template-rows: repeat(3,1fr);
  text-align: center;
  margin-bottom: 20px;
}
.about .tools {
  display: grid;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;
  grid-template-columns: repeat(1,1fr);
  text-align: center;
}
.Waterdrop {
  position: relative;
  height: 150px;
  width: 150px;
  margin: auto;
  border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%;
  opacity: 0.75;
  z-index: 1;
}
.Waterdrop::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background: #318CFE;
  border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%;
  box-shadow: 2px 1px 12px #1b6cfb, -10px 10px 12px #1b6cfb, inset -6px 6px 10px #1b6cfb, inset 2px 6px 10px #1a74e5, inset 10px -10px 12px white, 0px 10px 0px #a8ceff;
}
.Waterdrop::after {
  content: "";
  position: absolute;
  height: 40px;
  width: 40px;
  background: #E6FDFB;
  border-radius: 44% 56% 46% 54% / 36% 50% 50% 64%;
  left: 100px;
  top: 40px;
  box-shadow: 16px 40px 0 -10px white;
  opacity: 0.8;
}
.home .img {
  width:70px;
  z-index:1;
  /* border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%; */
  border-radius: 40% 50% 46% 44% / 36% 50% 38% 64%;
  position:absolute;
  left:55%;
  top:20%;
  opacity:0.75;
}
