  }
  .home .img {
    position: absolute !important;
    left: 69% !important;
    top: 21% !important;
  }
  /* .logo span {
    padding: 6px 10px;
    font-size: 18px;
  } */
  .home p {
    font-size: 27px;
  }
  .home h1 {
    font-size: 50px;
  }
  .home h2 {
    margin-top: 0.5rem !important;
    font-size: 20px;
  }
  .home h6 {
    font-size: 14px !important;
    position: absolute;
    top: 91%;
    left: 25%;
  }
  .about {
    font-size: 15px;
    display: grid;
    margin: auto 0;
    transform: translate(-40px);
    place-items: center;
  }
  .about .skills {
    margin-left: 0;
  }
  .about .frameworks {
    padding: 15px;
    min-width: 70vw;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .about .tools {
    padding: 15px;
    min-width: 70vw;
  }
  .tools-design {
    text-align: center;
  }
  .about i {
    margin-right: 5px;
    color: darkred !important;
  }
  .nav {
    height: 120px;
  }
  a.nav-link {
    padding: 0.35rem 0.5rem;
    margin-left:0.5rem !important;
  }
  .social-icons a {
    margin: 0 0.5rem 0.5rem 0;
    font-size: 1rem;
  }
  .grid {
    grid-template-columns: repeat(2,1fr);
  }
  .grid img {
    width: 90px;
    height: 90px;
  }
  .grid img:hover {
    transform: scale(1.2);
    transition: 300ms ease-in-out;
  }
  .grid2 {
    grid-template-columns: repeat(2,1fr);
  }
  .grid2 img {
    width: 90px;
    height: 90px;
  }
  .grid2 img:hover {
    transform: scale(1.2);
    transition: 300ms ease-in-out;
  }
  
}
