/* Bubbles */

.bubbles{
  position: absolute;
  width: 100%;
  height: 100%;
  
}
.bubble{
  position: absolute;
  bottom: 0;
  background: radial-gradient( #fff,#666666);
  border-radius: 50%;
  opacity: 0.8;
  animation: flying 15s infinite ease-in;
}
.bubble:nth-child(1){
  width: 40px;
  height: 40px;
  left: 10%;
  animation-duration: 8s;
}
.bubble:nth-child(2){
  width: 20px;
  height: 20px;
  left: 20%;
  animation-duration: 5s;
  animation-delay: 6s;
}
.bubble:nth-child(3){
  width: 50px;
  height: 50px;
  left: 35%;
  animation-duration: 5s;
  animation-delay: 4s;
}
.bubble:nth-child(4){
  width: 70px;
  height: 70px;
  left: 50%;
  animation-duration: 10s;
  animation-delay: 6s;
}
.bubble:nth-child(5){
  width: 35px;
  height: 35px;
  left: 55%;
  animation-duration: 6s;
  animation-delay: 4s;
}
.bubble:nth-child(6){
  width: 45px;
  height: 45px;
  left: 40%;
  animation-duration: 8s;
  animation-delay: 3s;
}
.bubble:nth-child(7){
  width: 25px;
  height: 25px;
  left: 75%;
  animation-duration: 7s;
  animation-delay: 5s;
}
.bubble:nth-child(8){
  width: 60px;
  height: 60px;
  left: 80%;
  animation-duration: 6s;
  animation-delay: 3s;
}
.bubble:nth-child(9){
  width: 15px;
  height: 15px;
  left: 70%;
  animation-duration: 9s;
  animation-delay: 3s;
}
.bubble:nth-child(10){
  width: 40px;
  height: 40px;
  left: 25%;
  animation-duration: 7s;
  animation-delay: 3s;
}
.bubble:nth-child(11){
  width: 15px;
  height: 15px;
  left: 70%;
  animation-duration: 9s;
  animation-delay: 4s;
}
.bubble:nth-child(12){
  width: 40px;
  height: 40px;
  left: 25%;
  animation-duration: 8s;
  animation-delay: 3s;
}
@keyframes flying{
  0%{
    bottom: 0px;
    transform: translateX(-20px);
  }
  50%{
    transform: translateX(100px);
  }
  100%{
    bottom: 100%;
    transform: translateY(-150px);
  }
}