@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);

*, *::before, *::after {
  box-sizing: border-box;
}
html {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans',sans-serif;
  background-color: #e6e7ee; 
  overflow-x: hidden;
}
nav {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  padding-top: 0.5rem;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  list-style: none;
  padding-top: 0.4rem;
  margin: 0 !important;
}

.nav-link {
  display: inline;
  padding: 0.45rem 1.2rem;
  text-decoration: none;
  font-weight: 300;
  margin-left:2rem !important;
  cursor: pointer;
  letter-spacing: 0.025em;
  border-radius: 0.55rem;
  color: rgb(92, 91, 91);
  font-weight: 500;
  border: 0.0625rem solid transparent;
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
  border: 0.0625rem solid #D1D9E6;
}

.nav-link:hover, .nav-link:focus {
  text-decoration: none;
  box-shadow: inset 2px 2px 5px #b8b9be,
              inset -3px -3px 7px #ffffff;
  transition: all 0.2s ease;
  border: 0.0625rem solid #D1D9E6;
}
.social-icons ul {
  display: -webkit-flex;
  display: flex ;
}
.social-icons a {
  border-radius: 2rem;
  color: #31344b;
  background-color: #e6e7ee;
  position: relative;
  margin: 0 1rem 1rem 0;
  font-size: 1.5rem;
  border: 0.0625rem solid #D1D9E6;
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
  width: 3.575rem;
  padding: 0.5rem;
  letter-spacing: 0.025em;
  height: 3.575rem;
}
.social-icons a:hover,
.social-icons a:focus {
  box-shadow:inset 3px 3px 6px #b8b9be,inset -3px -3px 6px #ffffff;
}
.fab  {
  color: rgb(92, 91, 91) !important;
  text-align: center;
  margin-left: 2px;
}
.logo {
  position: relative;
  top: 5%;
  left: 4%;
  z-index: 99;
}
