.wrapper {
  max-width: 1200px;
  color: rgb(92, 91, 91);
  font-weight: 500;
  margin: 0 auto;
  min-height: 100vh;
  margin-top: 0.8rem;
  background-clip: padding-box;
  border: 0.0625rem solid #e2e6ee;
  border-radius: 0.55rem;
  box-shadow: 2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff
}
.main {
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
  margin: 0.5rem;
  border: 0.0625rem solid #D1D9E6;
  padding: 0.7rem;
  border-radius: 0.55rem;
}
.inside-wrapper {
  box-shadow:  2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff;
  padding: 0.5rem;
 
  border-radius: 0.55rem;
}
.grid {
  display: grid;
  grid-column-gap: 0.8rem;
  -webkit-column-gap: 0.8rem;
          column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(4,1fr);
  text-align: center;

}
.grid img {
  width: 180px;
  height: 150px;
}
.grid img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: 300ms ease-in-out;
}
.grid2 {
  display: grid;

  border-radius: 0.55rem;
  grid-column-gap: 0.8rem;
  -webkit-column-gap: 0.8rem;
          column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(2,1fr);
  text-align: center;
}
.grid2-1 {
  color: red !important;
}
h3 {
  font-size: 0.875rem;
  padding: 1rem 1.5rem;
  border-radius: 0.55rem;
  border: 0.0625rem solid #D1D9E6;
  border-radius: 0.55rem;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;

}
