  opacity: 0.9;
  width: 100%;
  height: 80%;
}
.home p {
  font-size: 36px;
  margin-top: 0.5rem!important;
  font-weight: bold;
  margin: 0;
}
.home h2 {
  overflow: hidden;
  height: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
