.about {
  display: grid;
  grid-template-columns: repeat(1,1fr);
  display: grid;
  margin: auto 0;
  -webkit-transform: translate(-40px);
          transform: translate(-40px);
  place-items: center;
  margin-top: 20px;
  /* text-shadow:  5px 9px 5px #b8b9be,  -1px -1px 7px #ffffff !important;
  font-weight: 700; */
}
.about h1 {
  z-index: 99;
