}
.about .skills {
  min-height: 20vh;
  width: 50%;
  margin-left: 100px;
  margin-bottom: 20px;
}
.about .frameworks {
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;
  display: grid;
  height: 30vh;
  grid-template-rows: repeat(3,1fr);
  text-align: center;
  margin-bottom: 20px;
}
.about .tools {
  display: grid;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;
  grid-template-columns: repeat(1,1fr);
  text-align: center;
}
.Waterdrop {
  position: relative;
  height: 150px;
  width: 150px;
  margin: auto;
  border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%;
  opacity: 0.75;
  z-index: 1;
}
.Waterdrop::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background: #318CFE;
  border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%;
  box-shadow: 2px 1px 12px #1b6cfb, -10px 10px 12px #1b6cfb, inset -6px 6px 10px #1b6cfb, inset 2px 6px 10px #1a74e5, inset 10px -10px 12px white, 0px 10px 0px #a8ceff;
}
.Waterdrop::after {
  content: "";
  position: absolute;
  height: 40px;
  width: 40px;
  background: #E6FDFB;
  border-radius: 44% 56% 46% 54% / 36% 50% 50% 64%;
  left: 100px;
  top: 40px;
  box-shadow: 16px 40px 0 -10px white;
  opacity: 0.8;
}
.home .img {
  width:70px;
  z-index:1;
  /* border-radius: 51% 49% 48% 52% / 62% 44% 56% 38%; */
  border-radius: 40% 50% 46% 44% / 36% 50% 38% 64%;
  position:absolute;
  left:55%;
  top:20%;
  opacity:0.75;
}
