/* Bubbles */

.bubbles{
  position: absolute;
  width: 100%;
  height: 100%;
  
}
.bubble{
  position: absolute;
  bottom: 0;
  background: radial-gradient( #fff,#666666);
  border-radius: 50%;
  opacity: 0.8;
  -webkit-animation: flying 15s infinite ease-in;
          animation: flying 15s infinite ease-in;
}
.bubble:nth-child(1){
  width: 40px;
  height: 40px;
  left: 10%;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}
.bubble:nth-child(2){
  width: 20px;
  height: 20px;
  left: 20%;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}
.bubble:nth-child(3){
  width: 50px;
  height: 50px;
  left: 35%;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.bubble:nth-child(4){
  width: 70px;
  height: 70px;
  left: 50%;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}
.bubble:nth-child(5){
  width: 35px;
  height: 35px;
  left: 55%;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
.bubble:nth-child(6){
  width: 45px;
  height: 45px;
  left: 40%;
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.bubble:nth-child(7){
  width: 25px;
  height: 25px;
  left: 75%;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
}
.bubble:nth-child(8){
  width: 60px;
  height: 60px;
  left: 80%;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.bubble:nth-child(9){
  width: 15px;
  height: 15px;
  left: 70%;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
}
.bubble:nth-child(10){
  width: 40px;
  height: 40px;
  left: 25%;
  -webkit-animation-duration: 7s;
          animation-duration: 7s;
  -webkit-animation-delay: 3s;
